// frontend/src/services/target_config.ts
const YOUR_HOST_IP = '10.92.32.164'; //  IP    

const isGitHubPages = window.location.hostname.includes('github.io');
const isLocalhost = window.location.hostname.includes('localhost') || 
                    window.location.hostname.includes('127.0.0.1');

//  :  GitHub Pages  localhost
let apiBaseUrl: string;
let imgBaseUrl: string;

if (isGitHubPages) {
  // === :  ,    localhost ===
  apiBaseUrl = 'http://localhost:8000';  //     
  imgBaseUrl = 'http://localhost:9000';
  console.log(' GitHub Pages :   localhost:8000');
} else if (isLocalhost) {
  //  
  apiBaseUrl = 'http://localhost:8000';
  imgBaseUrl = 'http://localhost:9000';
} else {
  //  Tauri      (  )
  apiBaseUrl = `http://${YOUR_HOST_IP}:8000`;
  imgBaseUrl = `http://${YOUR_HOST_IP}:9000`;
}

export const CONFIG = {
  isGitHubPages,
  isLocalhost,
  apiBaseUrl,  //  "http://localhost:8000"   GitHub Pages
  imgBaseUrl,
  hostIP: YOUR_HOST_IP
};

export const DEFAULT_ISOTOPE_IMAGE = 'https://raw.githubusercontent.com/MaryEariel/isotope-calc6/main/default-isotope.jpg';